import { API } from '../constants'

const services = {
  list: API + '/orders/interactions',
  byid: API + '/orders/interactions',
  delete: API + '/orders/interactions',
  register: API + '/orders/interactions',
}

const _token = localStorage.getItem('__token')

export async function getAllOrderInteractions(id: number) {
  const requestOptions = {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.list}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export async function deleteOrderInteraction(id: any) {
  const requestOptions = {
    method: 'DELETE',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
  }
  let res = null

  await fetch(`${services.list}/${id}`, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}

export async function newOrderInteraction(body: any) {
  const requestOptions = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
      Authorization: 'Bearer ' + _token,
    },
    body: JSON.stringify(body),
  }
  let res = null

  await fetch(services.register, requestOptions).then((response) => {
    res = response.json()
  })

  return res
}
