<script setup lang="ts">
import { onMounted, ref } from 'vue'
import { useI18n } from 'vue-i18n'
import { useToast, VaIcon } from 'vuestic-ui'
import {
  deleteOrder,
  enableOrder,
  endOrder,
  getOrderById,
  setExpenseEmployee,
  setVehicleDriver,
} from '../../../core/requests/Orders'
import { useRoute, useRouter } from 'vue-router'
import { format } from 'date-fns'
import {
  deleteOrderInteraction,
  getAllOrderInteractions,
  newOrderInteraction,
} from '../../../core/requests/OrdersInteractions'
import Employee from '../../../core/requests/Employee'

const profileTabs = ref(0)
const showConfirm: any = ref(null)
const router = useRouter()
const { t } = useI18n()
const order: any = ref(null)
const comment = ref('')
const route = useRoute()
const interactions: any = ref([])
const employees: any = ref([])
const isLoading = ref(false)
const isLoadingFinalize = ref(false)
const isLoadingDelete = ref(false)
const showConditions = ref(false)
const activeConditions: any = ref(null)
const typeConditions: any = ref(0)
const isDeleting = ref(false)
const showConfirmDelete = ref(false)
const { notify } = useToast()

onMounted(() => {
  getData()
  getEmployees()
  getInteractions()
})

const getData = async () => {
  const data: any = await getOrderById(route.params.id)
  order.value = {
    _id: `000${data.id}-24`,
    ...data,
  }
}
const getEmployees = async () => {
  const data: any = await Employee.getActives()
  employees.value = data.map((d: any) => ({
    _id: `000${d.id}-24`,
    ...d,
  }))
}
const getInteractions = async () => {
  const data: any = await getAllOrderInteractions(+route.params.id)
  interactions.value = data.map((e: any) => ({
    ...e,
  }))
}
const formatDate = (date: any) => {
  return format(new Date(date), 'dd/MM/yyyy hh:mm aa')
}
const doRegister = async () => {
  isLoading.value = true
  await newOrderInteraction({
    comment: comment.value,
    quotation: +route.params.id,
    user: null,
  })

  await getInteractions()

  comment.value = ''
  isLoading.value = false
}
const doDelete = async (id: number) => {
  await deleteOrderInteraction(id)
}
const finalizeOrder = async () => {
  isLoadingFinalize.value = true
  await endOrder(+route.params.id)
  await getData()
  isLoadingFinalize.value = false
}
const doDeleteOrder = async () => {
  isLoadingDelete.value = true
  await deleteOrder(+route.params.id)
  await getData()
  showConfirmDelete.value = false
  isLoadingDelete.value = false
  router.push('/orders')
}
const deleteInteraction = async (e: any) => {
  e.loading = true
  await deleteOrderInteraction(e.id)
  await getInteractions()
  e.loading = false
}
const doEnableOrder = async () => {
  isLoadingFinalize.value = true
  await enableOrder(+route.params.id)
  await getData()
  isLoadingFinalize.value = false
}
const setDriver = async (vehicle: any, type = false) => {
  if (!type) {
    vehicle.loading = true
    await setVehicleDriver(
      {
        vehicle: vehicle.id,
        employee: vehicle.employee,
      },
      order.value.id,
    )
    vehicle.loading = false
  } else {
    vehicle.loading = true
    await setExpenseEmployee(
      {
        expense: vehicle.id,
        employee: vehicle.employee,
      },
      order.value.id,
    )
    vehicle.loading = false
  }
  notify({
    message: 'Trabajador asignado correctamente',
    color: 'success',
    duration: 1000,
  })
}
</script>

<template>
  <div v-if="order?._id">
    <div>
      <div class="flex justify-between align-center">
        <h1 class="h5 font-bold m-0">
          Orden de trabajo:
          <span class="text-primary font-bold pl-1">#{{ order._id }}</span> <br />
          <p class="text-sm font-medium">
            <VaBadge :text="order.service.services.service" color="primary" class="mr-2" />
          </p>
        </h1>
        <div class="flex gap-2">
          <div v-if="order.status !== 2" class="grid-cols-3 space-x-2.5 pt-4">
            <VaButton icon="save" color="success" :loading="isLoadingFinalize" @click="finalizeOrder">
              <span class="px-1">Finalizar Orden</span>
            </VaButton>
            <VaButton icon="delete" color="danger" :loading="isLoadingDelete" @click="showConfirmDelete = true">
              <span class="px-1">{{ t('actions.delete') }}</span>
            </VaButton>
          </div>

          <div v-if="order.status === 2" class="grid-cols-3 space-x-2.5 pt-4">
            <VaButton icon="save" color="info" :loading="isLoadingFinalize" @click="doEnableOrder">
              <span class="px-1">Activar Orden</span>
            </VaButton>
          </div>
        </div>
      </div>
      <hr class="mt-4 mb-2" />
    </div>

    <div class="py-4 order-detail flex columns-2 gap-4">
      <div class="w-1/3 py-5 pt-0 px-3">
        <div class="text-center relative">
          <div class="mt-2">
            <h4 class="text-base font-black text-primary">
              {{ order.customer.fullname }}
            </h4>
            <p class="text-xs font-bold">{{ order.customer.cellphone }}</p>
            <!-- <div class="flex gap-2 justify-center mt-2">
              <VaIcon class="material-symbols-outlined cursor-pointer"> mail </VaIcon>
              <VaIcon class="material-symbols-outlined cursor-pointer"> phone </VaIcon>
            </div> -->
            <div class="flex flex-wrap gap-2 mt-3 justify-center">
              <VaButton
                v-if="order.service.specifications"
                size="small"
                class="text-xxs"
                @click="
                  () => {
                    showConditions = true
                    typeConditions = 0
                    activeConditions = order.service.specifications
                  }
                "
              >
                Condiciones especificas
              </VaButton>
              <!-- <VaButton
                size="small"
                class="text-xxs"
                @click="
                  () => {
                    showConditions = true
                    typeConditions = 1
                  }
                "
              >
                Condiciones
              </VaButton> -->
            </div>

            <p class="text-xs mt-3 mb-3">
              <VaIcon size="14px" class="material-symbols-outlined cursor-pointer text-green-600">adjust</VaIcon>
              Ultima actualización {{ formatDate(order.updated_at) }}
            </p>
          </div>
        </div>

        <hr class="mt-5 mb-2" />

        <div>
          <VaTabs v-model="profileTabs" center grow class="new-customer-tabs">
            <template #tabs>
              <VaTab v-for="tab in ['Resumen', 'Vehiculos', 'Viaticos']" :key="tab" class="text-xs">
                {{ tab }}
              </VaTab>
            </template>

            <template #default>
              <div class="mt-4">
                <div v-if="profileTabs == 0">
                  <div class="bg-white p-4 rounded" square outlined>
                    <div class="flex flex-col gap-3">
                      <h2 class="text-sm font-bold text-primary">Detalle de servicio:</h2>

                      <div class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Ruta:</p>
                        <p>{{ order?.service.routes?.name }}</p>
                      </div>

                      <div class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Tipo de ruta:</p>
                        <p>{{ order?.service.route_type === 1 ? 'Ida y Vuelta' : 'Solo Ida' }}</p>
                      </div>

                      <div v-if="order?.service.kilometers" class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Kilometros:</p>
                        <p>{{ order?.service.kilometers }}</p>
                      </div>

                      <div v-if="order?.service.days" class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Nro Días:</p>
                        <p>{{ order?.service.days }}</p>
                      </div>

                      <div v-if="order?.service.passengers" class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Pasajeros:</p>
                        <p>{{ order?.service.passengers }}</p>
                      </div>

                      <hr v-if="order?.service.start_date" />

                      <div v-if="order?.service.start_date" class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Inicio aproximado:</p>
                        <p>{{ order?.service.start_date }}</p>
                      </div>

                      <div v-if="order?.service.end_date" class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Fin aproximado:</p>
                        <p>{{ order?.service.end_date }}</p>
                      </div>
                    </div>
                  </div>

                  <div class="bg-white p-4 rounded mt-3" square outlined>
                    <div class="flex flex-col gap-3">
                      <h2 class="text-sm font-bold text-primary">Costo de servicio:</h2>

                      <div class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Peajes:</p>
                        <p>S/ {{ order?.service.total_tolls }}</p>
                      </div>

                      <div class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Vehículos:</p>
                        <p>S/ {{ order?.service.total_vehicles }}</p>
                      </div>

                      <div class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Viaticos:</p>
                        <p>S/ {{ order?.service.total_expenses }}</p>
                      </div>

                      <div class="flex justify-between text-xs">
                        <p class="w-50 font-bold">Monto Total:</p>
                        <p>S/ {{ order?.service.total }}</p>
                      </div>
                    </div>
                  </div>
                </div>

                <div v-if="profileTabs == 1" class="bg-white p-4 rounded" square outlined>
                  <div class="flex flex-col gap-3">
                    <VaCard v-for="i in order?.vehicles" :key="i" color="#eff6ff">
                      <VaCardTitle class="capitalize">
                        <span
                          class="inline-flex items-center rounded-md bg-primary px-2 py-1 text-xxs font-bold text-white"
                        >
                          {{ i.passengers }} Pasajeros
                        </span>
                      </VaCardTitle>
                      <VaCardContent>
                        <p class="text-sm mb-1 font-semibold uppercase">{{ i.detail.name }}</p>
                        <div class="bg-white px-1.5 pt-2 pb-3 rounded">
                          <p class="text-xs font-semibold mb-2">
                            {{ i.typedriver }}
                          </p>

                          <VaSelect
                            v-model="i.employee"
                            placeholder="Selecciona un trabajador"
                            class="text-xss"
                            search-placeholder-text="Buscar"
                            text-by="names"
                            value-by="id"
                            :options="employees"
                            searchable
                            highlight-matched-text
                          />

                          <VaButton
                            size="small"
                            class="mr-6 mt-3 text-xxs"
                            :disabled="!i.employee"
                            :loading="i.loading"
                            @click="setDriver(i)"
                          >
                            Asignar conductor
                          </VaButton>
                        </div>

                        <p class="text-xs mt-2 flex justify-between"><b>Tarifa conductor:</b> S/ {{ i.driverfee }}</p>
                        <p class="text-xs mt-2 mb-1 flex justify-between"><b>Cantidad:</b> {{ i.quantity }} Unidades</p>

                        <p class="text-xs mt-2 flex justify-between"><b>Tarifa:</b> S/ {{ i.total }}</p>
                      </VaCardContent>
                    </VaCard>
                  </div>
                </div>
                <div v-if="profileTabs == 2" class="bg-white flex flex-col p-4 gap-2 rounded" square outlined>
                  <div class="flex flex-col gap-3">
                    <VaCard v-for="i in order.service.expenses" :key="i" color="#eff6ff">
                      <VaCardTitle class="capitalize">
                        <span
                          class="inline-flex items-center rounded-md bg-primary px-2 py-1 text-xxs font-bold text-white ring-1 ring-inset ring-gray-500/10"
                        >
                          {{ i.name }}
                        </span>
                      </VaCardTitle>
                      <VaCardContent>
                        <!-- <p class="text-xs mt-1 flex"><b class="pr-3">Cantidad:</b> {{ i.quantity }}</p> -->
                        <p class="text-xs mt-1"><b class="pr-2">Monto Unitario:</b> S/ {{ i.amount }}</p>
                        <div class="bg-white px-1.5 pt-2 pb-3 mt-3 rounded">
                          <p class="text-xs font-semibold mb-2">
                            {{ i.typedriver }}
                          </p>

                          <VaSelect
                            v-model="i.employee"
                            placeholder="Selecciona un trabajador"
                            class="text-xss"
                            search-placeholder-text="Buscar"
                            text-by="names"
                            value-by="id"
                            :options="employees"
                            searchable
                            highlight-matched-text
                          />

                          <VaButton
                            size="small"
                            class="mr-6 mt-3 text-xxs"
                            :disabled="!i.employee"
                            :loading="i.loading"
                            @click="setDriver(i, true)"
                          >
                            Asignar trabajador
                          </VaButton>
                        </div>
                      </VaCardContent>
                    </VaCard>
                  </div>
                </div>
              </div>
            </template>
          </VaTabs>
        </div>
      </div>
      <section class="flex flex-col w-2/3">
        <div class="bg-white p-3 mt-3">
          <div class="new-note mt-2">
            <VaInnerLoading :loading="isLoading">
              <VaCard color="#eff6ff">
                <VaCardTitle>Registrar un comentario</VaCardTitle>
                <VaCardContent>
                  <VaTextarea v-model="comment" class="w-full" background="#fff" clearable />
                </VaCardContent>
                <VaCardActions align="right">
                  <VaButton :disabled="!comment" @click="doRegister">Registrar comentario</VaButton>
                </VaCardActions>
              </VaCard>
            </VaInnerLoading>
          </div>

          <h3 class="text-xs font-bold my-4 underline">Historial de interacciones:</h3>

          <div class="list-notes">
            <div v-if="interactions.length">
              <VaListItem
                v-for="(i, index) in interactions"
                :key="index"
                class="list__item mt-3 bg-blue-50 px-2 py-3 rounded-md"
              >
                <VaListItemSection>
                  <VaListItemLabel class="flex justify-between text-primary font-bold text-xs">
                    <span class="capitalize">{{ i.creator.name }}</span>
                    <VaBadge :text="formatDate(i.created_at)" color="#fff" class="ml-3" />
                  </VaListItemLabel>
                  <VaListItemLabel class="text-black pt-2 text-xs" caption :lines="index + 10">
                    {{ i.comment }}
                  </VaListItemLabel>
                </VaListItemSection>
                <VaListItemSection icon>
                  <VaButton
                    round
                    class="cursor-pointer"
                    color="danger"
                    icon="delete"
                    :loading="i.loading"
                    @click="deleteInteraction(i)"
                  />
                </VaListItemSection>
              </VaListItem>
            </div>

            <VaAlert v-if="!interactions.length" class="text-xs" color="#fdeae7" text-color="#940909">
              No existe actividad en esta orden.
            </VaAlert>
          </div>
        </div>
      </section>
    </div>
  </div>

  <VaModal v-model="showConfirm" size="250px" hide-default-actions no-dismiss>
    <p class="text-sm">¿Deseas eliminar este comentario?</p>
    <template #footer>
      <VaButton size="small" preset="secondary" class="mr-1" @click="showConfirm = null"> Cancelar</VaButton>
      <VaButton size="small" :loading="isDeleting" @click="doDelete"> Continuar</VaButton>
    </template>
  </VaModal>

  <VaModal v-model="showConditions" size="600px" hide-default-actions no-dismiss>
    <p class="text-sm font-semibold text-primary">
      {{ !typeConditions ? 'Condiciones Especificas' : 'Condiciones Generales' }}
    </p>
    <hr class="my-3" />
    <div class="py-3 px-2 text-xs bg-white rounded" v-html="activeConditions"></div>
    <template #footer>
      <VaButton
        size="small"
        preset="secondary"
        class="mr-1"
        @click="
          () => {
            showConditions = false
            activeConditions = ''
          }
        "
      >
        Cerrar condiciones</VaButton
      >
    </template>
  </VaModal>

  <VaModal v-model="showConfirmDelete" size="250px" hide-default-actions no-dismiss>
    <p class="text-sm">¿Deseas eliminar esta orden?</p>
    <p class="text-xs mt-2">
      Se eliminara todo el historico de esta orden, el estado de la cotización volvera a
      <span class="font-bold">Aprobado</span>.
    </p>
    <template #footer>
      <VaButton size="small" preset="secondary" class="mr-1" @click="showConfirmDelete = false"> Cancelar</VaButton>
      <VaButton size="small" :loading="isDeleting" @click="doDeleteOrder"> Continuar</VaButton>
    </template>
  </VaModal>
</template>

<style lang="scss">
.order-detail {
  &-tabs {
    .va-tabs__content {
      width: 100%;
    }
  }

  .va-button--small .va-button__content {
    text-transform: none;
  }

  .va-input-wrapper__text {
    font-size: 11px;
  }
}
</style>
<div class=""></div>
